const months = {
  "01": "",
  "02": "",
  "03": "",
  "04": "",
  "05": "",
  "06": "",
  "07": "",
  "08": "",
  "09": "",
  10: "",
  11: "",
  12: "",
};

export default function getTime(time) {
  const year = time.slice(0, 4);
  const month = time.slice(5, 7);
  const day = time.slice(8, 10);
  return `${day} ${months[month]}`;
}
